'use strict';
var React = require('react/addons');
var Modal=require('react-bootstrap/lib/Modal');
var Input=require('react-bootstrap/lib/Input');
var Button=require('react-bootstrap/lib/Button');
var If=require('../commons/if.jsx');
var Reflux = require('reflux');
var Actions=require('../../actions/saveActions.js');
var CheckBox=require('../commons/customCheckbox.jsx')
var Store=require('../../stores/saveStore.js');
var TabbedArea = require('react-bootstrap/lib/TabbedArea');
var TabPane = require('react-bootstrap/lib/TabPane');

var _=require('lodash');

var Tags= React.createClass({displayName: "Tags",
	_update:function(event){
		this.props.onUpdate(event.target.value);
	},

	render:function() {
		return (
			React.createElement(Input, {ref: "tags", 
			className: "form-control taginput", 
			type: "text", maxLength: "80", 
			defaultValue: this.props.value, onBlur: this._update})
		);
	}
});


module.exports = React.createClass({displayName: "exports",

	mixins: [Reflux.connect(Store)],

	save:function(){
		//this._onClose();
		if(!this.state.saveAs && this.state.currentMap && this.state.currentMap._id){
			Actions.updateMap(this.state.currentMap._id, this.state.currentMap);
		} else {
			Actions.saveMap(this.state.currentMap);
		}
	},

	getInitialState:function(){
		return {'title':'', 'description':'', 'visible':false};
	},

	_updateTags:function(tags){
		var map = _.clone(this.state.currentMap);
		map.tags = tags;
		this.setState({'currentMap':map});
	},

	_updateTitle:function(event){
		var map = _.clone(this.state.currentMap);
		map.title = event.target.value;
		this.setState({'currentMap':map});
	},

	_updateDescription:function(event){
		var map = _.clone(this.state.currentMap);
		map.description = event.target.value;
		this.setState({'currentMap':map});
	},

	_onClose:function(){
		this.setState({'visible':false, 'errorMsg': ''});
	},

	_openSave:function(){
		this.setState({'visible':true, 'saveAs': false});
	},

	_openSaveAs:function(){
		this.setState({'visible':true, 'saveAs': true, 'currentMap': this.state.currentMap || {}});
	},

	render:function() {
		var errorArray = this.state.errorMsg?this.state.errorMsg.split(','):null;
		var saveLabel = this.state.saveAs? i18n.t('savemap.saveasnewbutton') : i18n.t('savemap.savebutton');
		return (
			React.createElement("div", {className: "save-map-trigger"}, 
			React.createElement("a", {href: "#"}, 
				React.createElement("i", {className: "fa fa-floppy-o", title: i18n.t('savemap.tooltipsaveasnewmap'), onClick: this._openSaveAs})
			), 
			this.state.currentMap._id?
				React.createElement("a", {href: "#"}, 
					React.createElement("i", {className: "fa fa-pencil-square-o", title: i18n.t('savemap.tooltipsavemap'), onClick: this._openSave})
				)
			: null, 
			React.createElement(Modal, React.__spread({animation: false, className: "dialog-save-map"},  this.props, {bsSize: "large", "aria-labelledby": "contained-modal-title-lg", 
			 show: this.state.visible, onHide: this._onClose}), 
				React.createElement(Modal.Header, null, 
					React.createElement(Modal.Title, null, 
						React.createElement("i", {className: "fa fa-folder-open"}), saveLabel
					), 
					React.createElement("a", {className: "close-dialog", href: "#", onClick: this._onClose}, 
					React.createElement("i", {className: "fa fa-times-circle-o"}))
				), 
				React.createElement(Modal.Body, null, 
					React.createElement("div", {className: "blue-panel"}, 
						React.createElement("div", null, 
							React.createElement(Input, {name: "title", 
								type: "text", 
								className: "form-control title", 
								onChange: this._updateTitle, 
								placeholder: i18n.t('savemap.savemaptitle'), 
								value: this.state.saveAs? '' : this.state.currentMap.title, maxLength: "100", addonAfter: "*"}), 
							React.createElement(Input, {type: "textarea", name: "description", 
								onChange: this._updateDescription, 
								className: "form-control description", 
								rows: "3", 
								placeholder: i18n.t('savemap.savemapdescription'), 
								value: this.state.saveAs? '' : this.state.currentMap.description, maxLength: "300", addonAfter: "*"})
						), 

						React.createElement("div", null, 
							React.createElement("h4", {className: "modal-title"}, React.createElement(Message, {message: "savemap.savemaptags"})), 
							React.createElement(Tags, {onUpdate: this._updateTags, value: this.state.saveAs? '' : this.state.currentMap.tags})
						), 
						React.createElement("div", {className: "required"}, React.createElement(Message, {message: "savemap.mandatoryFields"}), 
							
								_.map(errorArray, function(e){
									return (React.createElement(Message, {message: e}))
								})
							
						), 

						React.createElement("div", {className: "adjacent-buttons"}, 
							React.createElement(Button, {className: "btn btn-apply pull-right", onClick: this.save}, saveLabel), 
							React.createElement(Button, {className: "pull-right", onClick: this._onClose}, i18n.t('savemap.closebutton'))
						)
					)
				), 
				React.createElement(Modal.Footer, null
				)
			)
			)
		);
	}
});
