'use strict';
var React = require('react/addons');
var Modal=require('react-bootstrap/lib/Modal');
var Input=require('react-bootstrap/lib/Input');
var Button=require('react-bootstrap/lib/Button');
var If=require('../commons/if.jsx');
var Reflux = require('reflux');
var Actions=require('../../actions/saveActions.js');
var CustomRadio = require('../commons/customRadioButton.jsx').Radio;
var CustomRadioGroup = require('../commons/customRadioButton.jsx').RadioGroup;
var Store=require('../../stores/saveStore.js');

var _=require('lodash');

module.exports = React.createClass({displayName: "exports",

	mixins: [Reflux.connect(Store)],

	_export:function(){
		var activitiesEnabled = this.state.layersVisible.shapes || this.state.layersVisible.points;
		var type = !activitiesEnabled? 'indicators': this.state.type;
		this.setState({'exportDisabled': true});
		if(type=='activities'){
			Actions.exportActivities(this.state.format);
		} else {
			Actions.exportIndicators();
		}
	},

	getInitialState:function(){
		return {'type': 'activities', 'visible': false};
	},

	_exportTypeActivities:function(type){
		this.setState({'type': 'activities'});
	},

	_exportTypeIndicators:function(type){
		this.setState({'type': 'indicators'});
	},

	_onClose:function(){
		this.setState({'visible': false, 'error': ''});
	},

	_open:function(){
		if (this.state.type==='activities' && !(this.state.layersVisible.shapes || this.state.layersVisible.points)){
			this.setState({'type': 'indicators'});
		} else if (this.state.type==='indicators' && !this.state.layersVisible.indicators){
			this.setState({'type': 'activities'});
		}
		this.setState({'visible': true});
	},

	render:function() {
		var indicatorsEnabled = this.state.layersVisible.indicators;
		var activitiesEnabled = this.state.layersVisible.shapes || this.state.layersVisible.points;
		var type = !activitiesEnabled? 'indicators': this.state.type;
		var errorArray = this.state.error && this.state.error.split?this.state.error.split(','):null;
		return (
			React.createElement("div", {className: "save-map-trigger"}, 
			React.createElement("a", {href: "#"}, 
				React.createElement("i", {className: "fa fa-share-square-o", title: i18n.t('savemap.exportmap'), onClick: this._open})
			), 
			React.createElement(Modal, React.__spread({animation: false, className: "dialog-save-map"},  this.props, {bsSize: "large", "aria-labelledby": "contained-modal-title-lg", 
			 show: this.state.visible, onHide: this._onClose}), 
				React.createElement(Modal.Header, null, 
					React.createElement(Modal.Title, null, 
						React.createElement("i", {className: "fa fa-folder-open"}), React.createElement(Message, {message: "savemap.exportmap"})
					), 
					React.createElement("a", {className: "close-dialog", href: "#", onClick: this._onClose}, 
					React.createElement("i", {className: "fa fa-times-circle-o"}))
				), 
				React.createElement(Modal.Body, null, 
					React.createElement("div", {className: "blue-panel"}, 
						React.createElement("div", null, 
							React.createElement(CustomRadioGroup, null, 
								activitiesEnabled?
					              React.createElement(CustomRadio, {className: "horizontal", name: "activities", checked: type=='activities'? true : false, 
					              	onClick: this._exportTypeActivities, label: "savemap.exportactivities"})
					            : null, 
					            indicatorsEnabled?
					              React.createElement(CustomRadio, {className: "horizontal", name: "indicators", checked: type=='indicators'? true : false, 
					              	onClick: this._exportTypeIndicators, label: "savemap.exportindicators"})
					            : null
				            )
				        ), 
						errorArray?
							React.createElement("div", {className: "filter-no-results"}, React.createElement("br", null), 
								
									_.map(errorArray, function(e){
										return (React.createElement(Message, {message: e}))
									})
								
							)
						: null, 
						this.state.exportDisabled?
							React.createElement("div", null, React.createElement("img", {className: "img-centered", src: "images/ajax-loader-small.gif"}))
						:null, 
						React.createElement("div", {className: "adjacent-buttons"}, 
							React.createElement(Button, {className: "btn btn-apply pull-right", disabled: this.state.exportDisabled?'disabled':'', onClick: this._export.bind(this)}, i18n.t('savemap.exportbutton')), 
							React.createElement(Button, {className: "pull-right", onClick: this._onClose.bind(this)}, i18n.t('savemap.closebutton'))
						)
					)
				), 
				React.createElement(Modal.Footer, null
				)
			)
			)
		);
	}
});
